// header && footer

if (screen.width < 992) {
  console.log("mobile");
  $(document).ready(function() {
    const menuList = $("#menu-list");
    const burger = $("#burger");

    burger.click(function() {
      console.log("bb");
      $(this).toggleClass("burgerOpen");
      $("body").toggleClass("header-open");
    });

    $(document).on("click", menuList, function(e) {
      if ($(e.target).children("span")) {
        $(e.target)
          .next("ul")
          .toggleClass("open");
      }
    });

    $("footer")
      .find(".title")
      .click(function() {
        $(this)
          .parent()
          .toggleClass("open");
      });
  });
}

// slideres

$(document).ready(function() {
  const defaultSliderSettings = {
    slidesToScroll: 1,
    slidesToShow: 4,
    infinite: true,
    autoplay: true,
    autoplaySpeed: 3000,
    centerMode: true,
    arrows: true,
    dots: false,
    speed: 500,
    responsive: [
      {
        breakpoint: 1100,
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 767,
        settings: {
          slidesToShow: 1,
          variableWidth: false,
        },
      },
    ],
  };

  $(".slider-fixed-width").slick(defaultSliderSettings);

  $(".slider-standard-film").slick({
    ...defaultSliderSettings,
    variableWidth: true,
  });

  let target1 = 1;

  $("#zoomModal").on("show.bs.modal", function(e) {
    $(".zoomSlider").slick({
      ...defaultSliderSettings,
      initialSlide: parseInt(target1),
      slidesToShow: 1,
      responsive: [
        {
          breakpoint: 1100,
          settings: {
            slidesToShow: 1,
          },
        },
        {
          breakpoint: 767,
          settings: {
            slidesToShow: 1,
            variableWidth: false,
          },
        },
      ],
    });
  });

  $("#zoomModal").on("hidden.bs.modal", function(e) {
    $(".zoomSlider").slick("unslick");
  });

  $(".modalContainer").click(function(e) {
    if ($(this) === $(e.target)) {
      $("#zoomModal").modal("hide");
    }
  });
});

document.getElementById("toggleForm").addEventListener("click", (e) => {
  $("#formQuestions").toggle();
});
